import React from "react";
import { render, fireEvent } from "@testing-library/react-native";
import Home from "../screens/profils/Home";

// Mock navigation
jest.mock("@react-navigation/native", () => ({
  useNavigation: () => ({
    navigate: jest.fn(),
  }),
}));

// Mock LinearGradient
jest.mock("react-native-linear-gradient", () => ({ children }) => <>{children}</>);

// Mock transaction data
jest.mock("../utils/dataTrans", () => ({
  transaction: [
    { type: "dépense", amount: 1000 },
    { type: "revenu", amount: 500 },
    { type: "transfert", amount: 200 },
  ],
}));

// Mock TransactionCard
jest.mock("../utils/transactionCard", () => () => <></>);

// Mock ScrollView pour react-native-gesture-handler
jest.mock("react-native-gesture-handler", () => {
  const ActualGestureHandler = jest.requireActual(
    "react-native-gesture-handler/jestSetup"
  );
  return {
    ...ActualGestureHandler,
    ScrollView: jest.fn().mockImplementation(({ children }) => children),
  };
});

describe("Home Component", () => {
  it("rend correctement les sections principales", () => {
    const { getByText } = render(<Home />);

    expect(getByText("Bienvenue, Beriole")).toBeTruthy();
    expect(getByText("Portefeuille")).toBeTruthy();
    expect(getByText("Solde Total")).toBeTruthy();
    expect(getByText("10 000 000 000 000 FCFA")).toBeTruthy();
    expect(getByText("Dépenser")).toBeTruthy();
    expect(getByText("Déposer")).toBeTruthy();
    expect(getByText("Transférer")).toBeTruthy();
    expect(getByText("Historique")).toBeTruthy();
    expect(getByText("transactions Recentes")).toBeTruthy();
    expect(getByText("voir tout")).toBeTruthy();
  });

  it("permet de presser les boutons", () => {
    const { getByText } = render(<Home />);

    fireEvent.press(getByText("Dépenser"));
    fireEvent.press(getByText("Déposer"));
    fireEvent.press(getByText("Transférer"));
    fireEvent.press(getByText("Historique"));
  });
});
